
// Communicate with Django Server via API

export default {
  install: function (Vue, options) {
    let apiRoot = ''
    if (process.env.NODE_ENV === 'development') {
      Vue.prototype.$mediaRoot = 'https://hk-ayame-dev.oss-cn-hongkong.aliyuncs.com'
      apiRoot = 'http://192.168.0.20:18000'
    } else {
      Vue.prototype.$mediaRoot = 'https://hk-ayame-prod.oss-cn-hongkong.aliyuncs.com'
      apiRoot = 'https://ayame.tokyoedu.com'
    }

    Vue.prototype.fetchCourses = function (errFun) {
      if (this.$data.token === null) {
        console.log('moving to login page')
        this.$router.push({ path: 'login' })
        return false
      }

      fetch(apiRoot + '/courses/api/', {
        method: 'GET',
        headers: {
          Authorization: 'Token ' + this.$store.state.token
        }
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('get courses failed')
          }
          return response.json()
        })
        .then(data => {
          console.log('fetch success. storing data')
          this.$store.commit('setCourses', data)
          this.courses = data
        })
        .catch(error => {
          errFun()
          console.log('fetch course failed')
          console.log(error)
        })
    }

    Vue.prototype.fetchResponses = function (errFun) {
      if (this.$data.token === null) {
        console.log('moving to login page')
        this.$router.push({ path: 'login' })
        return false
      }

      fetch(apiRoot + '/responses/api/', {
        method: 'GET',
        headers: {
          Authorization: 'Token ' + this.$store.state.token
        }
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('fetchresponses failed')
          }
          return response.json()
        })
        .then(data => {
          this.$store.commit('setResponses', data)
          this.responses = data
        })
        .catch(error => {
          errFun()
          console.log('fetch responses failed')
          console.log(error)
        })
    }

    Vue.prototype.login = function (data, successFun, failureFun) {
      fetch(apiRoot + '/auth-token/', {
        method: 'POST',
        body: data
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('Login failed')
          }
          return response.json()
        })
        .then(data => {
          successFun(data.token)
        })
        .catch(failureFun())
    }

    Vue.prototype.postResponse = function (data, successFun) {
      // this.loading = true

      const formData = new FormData()
      for (var key in data) {
        formData.append(key, data[key])
      }

      fetch(apiRoot + '/responses/api/create_or_update', {
        method: 'POST',
        headers: {
          Authorization: 'Token ' + this.$store.state.token
        },
        body: formData
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('upload failed')
          }
          return response.json()
        })
        .then(data => {
          successFun()
          console.log('upload success')
        })
        .catch(error => {
          console.log('fetch postResponse failed')
          // this.loading = false
          console.log(error)
          this.snackbar = true
        })
    }
  }

}
