import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    courses: null,
    responses: null,
    currentChapterId: null,
    currentCourseId: null,
    currentQuestionCategoyId: null,
    currentQuestionId: null,
    currentRecordSrc: null,
    currentRecord: ''
  },
  mutations: {
    setToken (state, val) {
      state.token = val
    },
    setCourses (state, courses) {
      state.courses = courses
    },
    setResponses (state, responses) {
      state.responses = responses
    },
    setAPIserver (state, url) {
      state.apiServer = url
    },
    setAllCurrentIds (state, ids) {
      state.currentCourseId = ids.courseId
      state.currentChapterId = ids.chapterId
      state.currentQuestionCategoyId = ids.qcId
      state.currentQuestionId = ids.questionId
    },
    setCurrentQuestionId (state, qid) {
      state.currentQuestionId = qid
    },
    setCurrentRecordSrc (state, src) {
      state.currentRecordSrc = src
    },
    setCurrentRecord (state, dt) {
      state.currentRecord = dt
    },
    resetRecord (state) {
      state.currentRecordSrc = null
      state.currentRecord = ''
    },
    clearAll (state) {
      localStorage.clear()
      Object.keys(state).forEach(key => {
        state[key] = null
      })
    }

  },
  actions: {
  },
  getters: {
    currentCourse: state => {
      const cc = state.courses.find(course => course.id === state.currentCourseId)
      return cc
    },
    chapters: (state) => (courseId) => {
      return state.courses.find(course => course.id === courseId).chapters
    },
    currentChapter: (state, getters) => {
      return getters.currentCourse.chapters.find(chapter => chapter.id === state.currentChapterId)
    },
    questionCategories: (state, getters) => (courseId, chapterId) => {
      return getters.chapters(courseId).find(chapter => chapter.id === chapterId).qestion_categories
    },
    currentQuestionCategory: (state, getters) => {
      return getters.currentChapter.question_categories.find(qc => qc.id === state.currentQuestionCategoyId)
    },
    questions: (state, getters) => (courseId, chapterId, qcId) => {
      return getters.chapters(courseId).find(chapter => chapter.id === chapterId).qestion_categories.find(qc => qc.id === qcId).questions
    },
    currentQuestion: (state, getters) => {
      return getters.currentQuestionCategory.questions.find(q => q.id === state.currentQuestionId)
    },
    currentResponse: (state, getters) => {
      return state.responses.find(r => r.question_id === state.currentQuestionId)
    }

  },
  modules: {
  },
  plugins: [createPersistedState()]

})
